    import {  
        mapState,  
        mapMutations  
    } from 'vuex';  
    export default { 
		data() {
			return {
				act_info:[]
			}				
		},
        computed: mapState(['hasLogin','userInfo','Location']),  
		onLoad(e) {
			let _this = this;
			_this.city = _this.Location.city_name;
			console.log('页面加载');
			if (_this.hasLogin) { 
				_this.$Request.post('plat_act/act_subdetail',{id:e.sact_id}).then(res => {
					if (res.code == 200) {
						console.log(res);
						_this.act_info = res.datas.act_detail;
					}
					else{
						uni.showToast({
							icon: 'none',
							title:res.datas.error,
							duration:2000
						});
						
					}
				 });
			}
		},
         onReachBottom() {
             console.log('正在加载中...');
         },
         onPageScroll(){
             console.log('页面滚动...');
         },
         onPullDownRefresh() {
             console.log('上拉刷新...');
         },
        methods: {
            toPage: function(url) {
				if (!this.hasLogin) {
					url="/pages/member/login";
				} 
				uni.navigateTo({
					url:url,
				})
				
			},
			toCity:function(){
				uni.navigateTo({
					url:"/pages/index/city?url=/pages/member/points/pshop"
				})
			},

			 
        }  
    }  
